import logo from './logo.svg';
import 'bootstrap/dist/css/bootstrap.min.css';
import './Meu.css';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Image from 'react-bootstrap/Image';


function App() {
  return (
    <div className="App">
    <Form>
      <Image src={logo} className="mb-4" width="72" height="57"/>
      <h1 class="h3 mb-3 fw-normal">Please sign in</h1>
      <div class="caixa">
      <Form.Control type="email" class="controle" id="caixa_email" placeholder="name@example.com"/>
       <Form.Label for="caixa_email">Endereço de Email</Form.Label>
      </div>
      <div class="caixa">
        <Form.Control type="password" class="controle" id="floatingPassword" placeholder="Password"/>
        <Form.Label for="floatingPassword">Password</Form.Label>
      </div>
      <div class="checkbox mb-3">
        <Form.Check label="Remember me" value="remember-me"/>
      </div>
      <Button variant="primary" variant="outline-danger" size="lg" block type="submit">Sign in</Button>
      <p class="mt-5 mb-3 text-muted">&copy; 2017–2021</p>
    </Form>
    </div>
  );
}

export default App;

